import { API_URL } from "../../config";
import axios from "axios";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Comment from "./Comment";
import { makeStyles } from "@material-ui/core/styles";
import { Avatar, Card, CardContent, Grid, Button } from "@material-ui/core";
import moment from "moment";

const useStyles = makeStyles((theme) => ({
  root: {
    // display: "flex",

    // maxWidth: "50%",

    margin: 50,

    // flexDirection: "column",
    // flexGrow: 1,
    [theme.breakpoints.down("sm")]: {
      flexDirection: "column",
      width: "80%",
      heigh: "50%",
    },
    [theme.breakpoints.up("md")]: {
      flexDirection: "colum",
      width: "80%",
      heigh: "50%",
      // backgroundColor: theme.palette.secondary.main,
    },
    [theme.breakpoints.up("lg")]: {
      width: "70%",
      flexWrap: "wrap",
      height: "80%",
    },
  },
  content: {
    display: "flex",
    flexDirection: "column",
    maxWidth: "50%",
    flex: "1 0 auto",
    margin: 20,
    padding: 10,
    justify: "center",
  },
  image: {
    marginLeft: 40,
    width: 350,
    height: 400,
  },
  paper: {
    padding: 0,
    textAlign: "center",
    color: theme.palette.text.secondary,
    margin: 10,
  },
  btn: {
    margin: theme.spacing(1),
    height: 20,
    width: 10,
  },
  button: {
    display: "flex",
    flexDirection: "row",
    margain: 0,
    justifyContent: "flex-start",
  },
}));

function ActivityDetails(props) {
  const classes = useStyles();

  const [activityDetail, updateActiviy] = useState(null);

  const { onCreateComment, comments } = props;
  const [creater, updatecreater] = useState(false);
  const [isJoin, updatejoin] = useState(false);

  useEffect(() => {
    try {
      async function getData() {
        let activityId = props.match.params.id;
        let response = await axios.get(`${API_URL}/api/activity/${activityId}`);
        updateActiviy(response.data);
      }

      getData();
    } catch (err) {
      console.log("Activity fetch failed", err);
    }
  }, []);

  useEffect(() => {
    if (props.user && activityDetail) {
      if (props.user._id === activityDetail.creater._id) {
        updatecreater(true);
      }
    }
  });

  useEffect(() => {
    if (props.user && activityDetail) {
      for (let i = 0; i < activityDetail.joins.length; i++)
        if (activityDetail.joins[i]._id === props.user._id) {
          updatejoin(true);
        }
    }
  });

  console.log(isJoin);

  if (!activityDetail) {
    return <p>Loading</p>;
  }

  return (
    <div>
      <Grid container justify="center">
        <Card className={classes.root}>
          <CardContent className={classes.button}>
            <h1> {activityDetail.name}</h1>
            {creater ? (
              <p>
                <Button
                  variant="outlined"
                  className={classes.btn}
                  component={Link}
                  to={`/activity/${activityDetail._id}/edit`}
                >
                  Edit
                </Button>
                <Button
                  variant="outlined"
                  className={classes.btn}
                  onClick={() => {
                    props.onDelete(activityDetail._id);
                  }}
                >
                  Delete
                </Button>
              </p>
            ) : (
              <p></p>
            )}

            {!isJoin && props.user ? (
              <p>
                <Button
                  variant="outlined"
                  className={classes.btn}
                  onClick={(event) => {
                    props.onHandleJoin(event, activityDetail._id);
                  }}
                >
                  Join
                </Button>
              </p>
            ) : (
              <p></p>
            )}
          </CardContent>
          {/* <div className={classes.root}> */}
          <Grid container spacing={1}>
            <img className={classes.image} src={activityDetail.image} />
            Host:
            <Avatar src={activityDetail.creater.image} alt="User" />
            Join:
            <>
              {activityDetail.joins.map((elem) => (
                <Avatar src={elem.image} alt="joiner" />
              ))}
            </>
          </Grid>
          {/* </div> */}
          <Grid className={classes.content}>
            Location: {activityDetail.location}
            <br />
            {moment(activityDetail.date).format("DD-MMM-yyyy (ddd)")}
            <br />
            Time : {activityDetail.time}
            <br />
            <p>Description: {activityDetail.description}</p>
            <br />
            <p>
              <Comment
                user={props.user}
                activityDetail={activityDetail}
                comments={comments}
                onCreateComment={(event) =>
                  onCreateComment(event, activityDetail)
                }
              />
            </p>
          </Grid>
        </Card>
      </Grid>
    </div>
  );
}

export default ActivityDetails;
